const CitySearch = lazy(() => import('./CitySearch'));
const NumberOfEvents = lazy(() => import('./NumberOfEvents'));
const WelcomeScreen = lazy(() => import('./WelcomeScreen'));
const Illustration = lazy(() => import('./Illustration'));
const EventGenre = lazy(() => import('./EventGenre'));

class App extends Component {
  constructor(){
    super()
    this.state = {
      // scatter chart read from data state
      data:[],
      events: [],
      locations: [],
      locationCurrent: '',
      numberOfEvents: '',
      eventsToShow:[],
      showWelcomeScreen: undefined,
      onlineErr: '',
      error: {
        location: ''
      }
    }
  }
  
  //! FOR TESTING
  async componentDidMount() {
    this.mounted = true
    this.setState({ showWelcomeScreen: false })
    getEvents().then(events => {
      console.log(events)
      if (this.mounted){
        this.setState({
          events:[...events],
          locations: extractLocations(events),
          })
          this.getData()
      }
    })
  }
  

  // async componentDidMount() {
  //   this.mounted = true
    
  //   let testConnection = await checkOnlineStatus()
  //     if (testConnection.status !== 200) {
  //       this.setState({
  //         onlineErr: 'Offline'
  //       })
  //     }
  //     const accessToken = localStorage.getItem('access_token')
  //     const isTokenValid = (await checkToken(accessToken)).error ? false : true
  //     const searchParams = new URLSearchParams(window.location.search)
  //     const code = searchParams.get('code')
  //     this.setState({ showWelcomeScreen: !(code || isTokenValid )})
  //   if((code || isTokenValid) && this.mounted){
  //     if(this.state.onlineErr !== ''){
  //       this.setState({showWelcomeScreen: true})
  //     }
  //     getEvents().then(events => {
  //       this.setState({
  //         events:[...events],
  //         locations: extractLocations(events),
  //         })
  //         this.getData()
  //     })
  //   }
  // }

  componentWillUnmount() {
    this.mounted = false
  }
  
  
  updateEvents = async (location, number) => {
    this.setState({
      error:{
        location: ''
      }
    })
    getEvents().then(async events => {
      const clone = (arr) => Array.from(arr);
      let locationEvents = []
      this.setState({
        events:[...events]
      })
      
      
      if(location === 'all'){
        locationEvents = events
        this.setState({
          locationCurrent: '',
          numberOfEvents:''
        })
        return this.state.events
      }
      if(this.state.locationCurrent !== ''){
        this.setState({
          events: events.filter(event => event.location === this.state.locationCurrent)
        })
      }
      if(typeof(location) == 'string' && location !== 'all'){
        locationEvents = events.filter(event => event.location === location)
        this.setState({
          locationCurrent: location,
          events: locationEvents,
          numberOfEvents:''
        })
      }
      
      let selectedArray = this.state.events.slice(0, !number ? 32 : number);
      console.log(selectedArray.length)
      
      if(selectedArray.length < number){
        console.log(this.state.locationCurrent)
        if(this.state.locationCurrent === ''){
          return this.setState({
            error: {
              location: `All events are shown.`
            }
          })
        }
        return this.setState({
          error: {
            location: `All events for ${this.state.locationCurrent} are shown.`
          }
        })
      }
      this.setState({
        events: clone(selectedArray)
      })
      await this.getData()
    })
  }

  getData = () => {
    // events need to reflect what is shown in eventList
    const { locations, events } = this.state
    const data =  locations.map((location) => {
      const number = events.filter(event => event.location === location).length
      const city = location.split(', ').shift()
      return { city, number }
    })
    // data is stored in state
    return this.setState({
      data: data
    })
  }

  render(){
    const renderLoader = <p className='Alert'>This is app loading</p>;
    if(this.state.showWelcomeScreen === undefined){
      return (
        <div className="App" />
      )
    }
    return (
      <div className="App">
      <Suspense fallback={renderLoader}>
        <Illustration className='App__background' />
      </Suspense>
      <Navbar />
        <div className='main__container'>
          <Suspense fallback={renderLoader}>
            <div className='container__data'>
            
              <div className='chart'>
                  <Suspense fallback={renderLoader}>
                    <EventGenre events={this.state.events} />
                  </Suspense>
                  <ResponsiveContainer className='container__graph-scatter' height='100%'>
                    <ScatterChart
                    className='chart__scatter'
                      margin={{
                        top: 100,
                        right: 100,
                        bottom: 20,
                        left: 100,
                      }}
                    >
                      <CartesianGrid stroke="#F4F7E9" fill='hsla(217, 62%, 39%, .55)'/>
                      <XAxis type="category" dataKey="city" name="city" stroke='#eaf8bf'/>
                      <YAxis type="number" dataKey="number" name="number" allowDecimals={false} stroke='#eaf8bf'/>
                      <Tooltip cursor={{ strokeDasharray: '8 8'}} active={true}/>
                      <Scatter name="Events" data={this.state.data} fill='#20488A' animationBegin={500}>
                        <LabelList dataKey="number" position='top' stroke='#eaf8bf' fill='#eaf8bf'/>
                      </Scatter>
                    </ScatterChart>
                  </ResponsiveContainer>
              </div>
              
              
              <div className='container__input'>
                <div className='container__input-inner'>
                  <OnlineAlert  modifier={this.state.onlineErr === 'Offline' ? 'online-active' : 'online-hidden'} text={this.state.onlineErr} />
                  {/* <Suspense fallback={renderLoader}> */}
                    <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} />
                    <NumberOfEvents  events={this.state.events} errAlert={this.state.error.location} number={this.state.numberOfEvents} locations={this.state.locations} updateEvents={this.updateEvents}/>
                  {/* </Suspense> */}
                </div>
              </div>
            </div>
              
          </Suspense>
          <EventList events={this.state.events} />
        </div>
        <Suspense fallback={renderLoader}>
          <WelcomeScreen showWelcomeScreen={this.state.showWelcomeScreen} getAccessToken={() => getAccessToken() }/>
        </Suspense>
      </div>
    );
  }
}

export default App;
